* {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0 auto;
}

ul {
  padding: 0;
  margin: 0;
}

li, a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1, h2, p, figure, blockquote {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  cursor: hand;
}
